export class InstrumentoModel {
  public codigo: string;
  public descripcion: string;
  public fechaInicio: Date;
  public fechaTermino: Date;
  public isIniciado : boolean;

  constructor(fields?: {
    codigo?: string,
    descripcion?: string,
    fechaInicio?: Date,
    fechaTermino?: Date,
    isIniciado?: boolean,
  }) {
    if (fields) { Object.assign(this, fields); }
  }
}
