import { Component, OnInit } from '@angular/core';
import { FormControl, FormGroup, Validators } from '@angular/forms';
import { Route, Router } from '@angular/router';
import { BlockUI, NgBlockUI } from 'ng-block-ui';
import { ToastrService } from 'ngx-toastr';
import { SearchPagination } from 'src/app/commons/interfaces/search.pagination';
import { InstrumentoModel } from 'src/app/commons/models/evaluacion-docente/mantenedores/instrumentos.model';
import { MantenedoresEvaluacionDocenteService } from 'src/app/commons/services/evaluacion-docente-services/mantenedores.service';
import { UtilAlert } from 'src/app/commons/util/util.alert';
import Swal from 'sweetalert2';

@Component({
  selector: 'app-mantenedor-instrumentos',
  templateUrl: './mantenedor-instrumentos.component.html',
  styles: [
  ]
})
export class MantenedorInstrumentosComponent implements OnInit {

  public listadoInstrumentos: InstrumentoModel[] = [];

  public isModoEdicion: boolean = false;
  public isEdit: boolean = false;
  public isDuplicar: boolean = false;

  public instrumentoADuplicar = new InstrumentoModel();

  //** PAGINATION PREGUNTAS */
  public searchPagination: SearchPagination<string> = null;
  public pageSize: number = 10;
  public page: number = 1;
  public totalElements = 0;
  public buscador: string = '';

  public formTopBar: FormGroup;
  public formInstrumento: FormGroup;
  //** */

  public loading = false;
  @BlockUI('mantenedorInstrumentosEvaluacionDocente') blockUI: NgBlockUI;

  constructor(
    private mantenedorService: MantenedoresEvaluacionDocenteService,
    private toastr: ToastrService,
    private utilAlert: UtilAlert,
    private router: Router
  ) { }

  ngOnInit(): void {
    this.initFormTopBar();
    this.listarInstrumentos();
  }

  initFormTopBar() {
    this.formTopBar = new FormGroup({
      buscador: new FormControl(""),
      registros: new FormControl("")
    })

    this.formTopBar.get("buscador").setValue(this.buscador);
    this.formTopBar.get("registros").setValue(this.pageSize.toString());
  }

  initFormInstrumento(instrumento: InstrumentoModel) {
    this.formInstrumento = new FormGroup({
      codigo: new FormControl("", [Validators.required]),
      descripcion: new FormControl("", [Validators.required]),
      fechaInicio: new FormControl("", [Validators.required]),
      fechaTermino: new FormControl("", [Validators.required])
    })

    this.isEdit = instrumento != null;
    if (instrumento != null) {
      this.formInstrumento.get("codigo").setValue(instrumento.codigo);
      this.formInstrumento.get("descripcion").setValue(instrumento.descripcion);
      this.formInstrumento.get("fechaInicio").setValue(instrumento.fechaInicio);
      this.formInstrumento.get("fechaTermino").setValue(instrumento.fechaTermino);
    }
  }

  listarInstrumentos() {
    this.listadoInstrumentos = [];
    if (this.searchPagination == null) {
      this.searchPagination = {
        page: this.page,
        records: this.pageSize,
        seek: this.buscador,
      }
    } else {
      this.searchPagination = null
      this.listarInstrumentos();
    }
    this.mantenedorService.paginationInstrumentos(this.searchPagination).subscribe(result => {
      this.listadoInstrumentos = result.content;
      this.totalElements = result.totalElements;
    })
  }

  filtrarPorCodigo(valor: string) {
    this.buscador = valor;
    this.listarInstrumentos();
  }


  iniciarEdicionInstrumento(instrumento: InstrumentoModel) {
    this.initFormInstrumento(instrumento);
    this.isModoEdicion = true;
  }

  iniciarDuplicarInstrumento(instrumento: InstrumentoModel) {
    Swal.fire({
      title: '¿Estás seguro?',
      text: "Se duplicará unicamente la configuración de preguntas y respuestas",
      icon: 'warning',
      showCancelButton: true,
      confirmButtonColor: '#02BC77',
      cancelButtonColor: '#d33',
      confirmButtonText: 'Confirmar',
      cancelButtonText: 'Cancelar',
      reverseButtons: true
    }).then((result) => {
      if (result.isConfirmed) {
        this.instrumentoADuplicar = instrumento;
        this.isModoEdicion = true;
        this.isDuplicar = true;
        this.initFormInstrumento(null);
      }
    })
  }

  setFormToInstrumento() : InstrumentoModel {
    let instrumento = new InstrumentoModel();
    instrumento.codigo = this.formInstrumento.get("codigo").value.toUpperCase();
    instrumento.descripcion = this.formInstrumento.get("descripcion").value;
    instrumento.fechaInicio = this.formInstrumento.get("fechaInicio").value;
    instrumento.fechaTermino = this.formInstrumento.get("fechaTermino").value;
    return instrumento;
  }

  saveUpdateInstrumento() {
    if (!this.formInstrumento.valid) {
      this.toastr.error("El formulario no es valido.");
    } else {
      this.blockUI.start("Guardando cambios...");
      let instrumento = this.setFormToInstrumento();

      if (this.isEdit) {
        this.mantenedorService.updateInstrumento(instrumento).subscribe(result => {
          this.volver();
          this.listarInstrumentos();
          this.utilAlert.infoSwal("Instrumento editado exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      } else {
        this.mantenedorService.saveInstrumento(instrumento).subscribe(result => {
          this.volver();
          this.listarInstrumentos();
          this.utilAlert.infoSwal("Instrumento creado exitosamente.");
        }, (_error) => {
          this.toastr.error(_error?.error?.message);
        })
      }
      this.blockUI.stop();
    }
  }

  duplicarInstrumento() {
    if (!this.formInstrumento.valid || this.formInstrumento.get("codigo").value == this.instrumentoADuplicar.codigo) {
      this.toastr.error("El formulario no es valido.");
    } else {

      let instrumento = this.setFormToInstrumento();
      this.blockUI.start("Duplicando instrumento...");

        this.mantenedorService.duplicarInstrumento(instrumento, this.instrumentoADuplicar.codigo).subscribe(result => {
          this.volver();
          this.listarInstrumentos();
          this.blockUI.stop();
          this.utilAlert.infoSwal("Instrumento duplicado exitosamente.");
        }, (_error) => {
          this.blockUI.stop();
          this.toastr.error(_error?.error?.message);
        })
      
    }
  }

  volver() {
    this.isModoEdicion = false;
    this.isDuplicar = false;
  }

  configurarInstrumento(codigo: string){
    this.router.navigate([`/evaluacion-docente/config-instrumento/${codigo}`]);
  }

}
